@font-face {
    font-family: Din;
    src: url(/fonts/DIN\ Medium.ttf);
}

@font-face {
    font-family: light;
    src: url(/fonts/DIN.ttf);
}

@font-face {
    font-family: Lora;
    src: url(/fonts/Lora-VariableFont_wght.ttf);
}


body{
    margin: 0;
    padding: 0;
    color: black;
    background-color: #FEFAF6;
}

//darkmode 
.darkmode{
    background-color: #22092C;
    color: white;
}

.darkmode a{
    color: white;
}

//navbar css
.navbar img{
  width: 5%;
}

.navbar{
    display: flex;
    flex-direction: row;
    width: 100%;
    align-items: center;
    justify-content: space-around;
    font-family: Din;
    font-size: large;
    margin-top: 2%;
}

.navbar nav{
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    width: 50%;
}

.navbar nav a{
    text-decoration: none;
    color: black;
}


.navbar .icons{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    width: 6%;
}

// css for login_body
.login_body{
    display: flex;
    flex-direction: row;
    width: 100%;
    justify-content: space-evenly;
    margin-top: 7%;
    align-items: center;
}

.form{
    font-size: x-large;
    font-family: Lora;
}

.form label{
    font-family: din;
    font-size: smaller;
}

.form h1{
    font-family: lora;
    color: orangered;
}

.form img{
    width: 20%;
}

.form input{
    height: 30px;
    font-family: Din;
}

button{
    width: 50%;
    height: 29px;
    border: none;
    color: #FEFAF6;
    transition: background-color 1s ease-in-out , border 1s ease-in-out;
}

button:hover{
    background-color:orangered;
    border: #FEFAF6 1px solid;
}

// css for the footer 
footer{
    display: flex;
    flex-direction: column;
    width: 100%;
    align-items: flex-start;
    margin-top: 10%;
    background-color: #22092C;
    color: #FEFAF6;
    padding-top: 2%;
    padding-bottom:2% ;
}

.first_row{
    display: flex;
    flex-direction: row;
    align-items: center;
    width: 100%;
    justify-content: space-around;
    font-family: Din;
}

.column1_text{
    display: flex;
    flex-direction: row;
    align-items: center;
}

.column1_text p{
    margin-left: 2%;
}

.column1_text ion-icon{
    font-size: larger;
}

.column1{
    width: min-content;
}

textarea{
    background-color: beige;
}

button{
    font-family: Din;
    background-color: #22092C;
    color: wheat;
    border-radius: 2%;
}

hr{
    color: white;
    width: 95%;
    font-size: smaller;
}

.footericons{
    display: flex;
    font-size: x-large;
    width: 100%;
    justify-content: center;
    margin-top: 1.5%;
}

.footericons ion-icon{
    margin-left: 2%;
}